	            var a = parseFloat(parameters[1]);
	            var b = parseFloat(parameters[2]);
	            var c = parseFloat(parameters[3]);
	            var d = parseFloat(parameters[4]);
	            var e = parseFloat(parameters[5]);
	            var f = parseFloat(parameters[6]);

