	    /**
	     * Whether the left mouse button is currently pressed.
	     * @type Boolean
	     */
	    this.left = left;

	    /**
	     * Whether the middle mouse button is currently pressed.
	     * @type Boolean
	     */
	    this.middle = middle;

	    /**
	     * Whether the right mouse button is currently pressed.
	     * @type Boolean
	     */
	    this.right = right;

