	    var touch_count = 0;
	    var last_touch_x = 0;
	    var last_touch_y = 0;
	    var last_touch_time = 0;
	    var pixels_moved = 0;

	    var touch_buttons = {
	        1: "left",
	        2: "right",
	        3: "middle"
	    };

	    var gesture_in_progress = false;
	    var click_release_timeout = null;

